header.header {
    height: 50px;
    // background-color: rgba(255, 255, 255, 0.5);
    background-color: lightgrey;
    display: flex;
    align-items: center;
    padding: 0 12px;

    .container {
        width: 100%;
        max-width: 1000px;
        margin: 0 auto;
    }

    @media (min-width: 768px) {
        height: 60px;
    }

    .logo {
        img {
            width: 60px;

            @media (min-width: 768px) {
                width: 80px;
            }
        }
    }
}
